<template>
  <div class="detail">
    <detail-nav-bar class="navbar" @titleClick="titleClick" :currentIndex='currentIndex' ref="detailNav"></detail-nav-bar>
    {{$store.state.cartList.length}}
    <scroll class="wrapper" ref="scroll" @scroll="scroll" :probe-type='3'>
      <detail-swiper :topImages="topImages"> </detail-swiper>
      <detail-base-info :goods="goods"></detail-base-info>
      <detail-shop-info :shop="shop"></detail-shop-info>
      <detail-goods-info
        ref="goods"
        :detailInfo="detailInfo"
        @imageLoad="imageLoad"
      ></detail-goods-info>
      <detail-param-info ref="param" :paramInfo="paramInfo"></detail-param-info>
      <detail-comment-info
        ref="comment"
        :commentInfo="commentInfo"
      ></detail-comment-info>
      <goods-list ref="recommends" :goods="recommends"></goods-list>
    </scroll>
    <detail-bottom-bar @addToCart='addToCart'></detail-bottom-bar>
     <back-top @click.native="backClick" v-show="showBackTop"></back-top>
     <toast :message='message' :isShowed = "isShowed"></toast>
  </div>
  
</template>
<script>
import DetailNavBar from "./childComps/DetailNavBar";
import BackTop from 'components/content/backTop/BackTop'
import {
  getDetail,
  Goods,
  Shop,
  GoodsParam,
  getRecommend,
} from "network/detail.js";
import DetailSwiper from "./childComps/DetailSwiper";
import DetailBaseInfo from "./childComps/DetailBaseInfo";
import DetailShopInfo from "./childComps/DetailShopInfo";
import DetailGoodsInfo from "./childComps/DetailGoodsInfo";
import Scroll from "components/common/scroll/Scroll";
import DetailParamInfo from "./childComps/DetailParamInfo";
import DetailCommentInfo from "./childComps/DetailCommentInfo";
import GoodsList from "components/content/goods/GoodsList";
import { debounce } from "common/utils";
import { itemListenerMinxin,backTopMixin } from "common/mixin.js";
import DetailBottomBar from './childComps/DetailBottomBar'
import Toast from 'components/common/toast/Toast'
export default {
  name: "Detail",
  components: {
    DetailNavBar,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    DetailGoodsInfo,
    Scroll,
    DetailParamInfo,
    DetailCommentInfo,
    GoodsList,
    DetailBottomBar,
    BackTop,
    Toast
  },
 
  data() {
    return {
      iid: null,
      topImages: [],
      goods: {},
      shop: {},
      detailInfo: {},
      paramInfo: {},
      commentInfo: {},
      recommends: [],
      themeTops: [],
      getThemeTopY: null,
      currentIndex:0,
      message:'',
      isShowed:false
     
    };
  },
  created() {
    this.iid = this.$route.params.iid;
    getDetail(this.iid).then((res) => {
      const data = res.result;
      //1.获取轮播图数据
      this.topImages = res.result.itemInfo.topImages;
      console.log(res.result);
      //2.获取商品数据
      this.goods = new Goods(
        data.itemInfo,
        data.columns,
        data.shopInfo.services
      );
      //3.获取店铺数据
      this.shop = new Shop(data.shopInfo);
      //4.获取商品详情信息
      this.detailInfo = data.detailInfo;
      //5.获取商品参数信息
      this.paramInfo = new GoodsParam(
        data.itemParams.info,
        data.itemParams.rule
      );
      //6.获取评论信息
      if (data.rate.list) {
        this.commentInfo = data.rate.list[0];
      }
    });

    this.getThemeTopY = debounce(() => {
        this.themeTops = [];
      this.themeTops.push(0);
      this.themeTops.push(this.$refs.goods.$el.offsetTop);
      this.themeTops.push(this.$refs.param.$el.offsetTop);
      this.themeTops.push(this.$refs.comment.$el.offsetTop);
    });

    getRecommend().then((res) => {
      this.recommends = res.data.list;
    });
  },

  mixins: [itemListenerMinxin,backTopMixin],
  methods: {
    imageLoad() {
      this.$refs.scroll.refresh();
      this.getThemeTopY();
    },
    titleClick(index) {
      this.$refs.scroll.scroll.scrollTo(0, -this.themeTops[index] + 44, 100);
    },
    scroll(position) {
        if(-position.y < this.themeTops[1]) {
             this.$refs.detailNav.currentIndex = 0;
        }
        if(-position.y > this.themeTops[1]) {
            this.$refs.detailNav.currentIndex = 1;
        
        }
        if(-position.y > this.themeTops[2]) {
              this.$refs.detailNav.currentIndex = 2;
        }
        if(-position.y > this.themeTops[3]) {
              this.$refs.detailNav.currentIndex = 3;
        }

        this.showBackTop = -position.y > 2000;
    },
    addToCart() {
        const product = {}
        product.image = this.topImages[0];
        product.title = this.goods.title;
        product.desc = this.goods.desc;
        product.price = this.goods.realPrice;
        product.iid = this.iid
        this.$store.dispatch('addCart',product).then(resolve => {
         this.message = resolve;
         this.isShowed = true;
         setTimeout(()=>{this.isShowed = false;},1000)
        });

     
    }
 
  },
};
</script>
<style scoped>
.detail {
  position: relative;
  z-index: 9;
  height: 100vh;
  background-color: #fff;
}
.navbar {
  position: relative;
  z-index: 9;
  background-color: #fff;
}
.wrapper {
  height: calc(100% - 44px - 49px);
}
</style>