<template>
    <div class="toast" v-show="isShowed">
        {{message}}
    </div>
</template>
<script>
export default {
    name:'Toast',
    props: {
        message:{
            type:String,
            default:''
        },
        isShowed: {
            type:Boolean,
            default:false
        }
    }
}
</script>
<style scoped>
.toast {
    position: fixed;
    line-height: 30px;
    color: white;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: rgba(10, 0, 0, 0.5);
    z-index: 9;
}
</style>